import { handleNoResult, getText, getInnerHTML } from './index';
var HOST = 'https://cn.bing.com';
export function handleLexResult(doc, options, transform) {
    var searchResult = {
        result: {
            type: 'lex',
            title: getText(doc, '.client_def_hd_hd', transform),
        },
    };
    // pronunciation
    if (options.phsym) {
        var $prons = Array.from(doc.querySelectorAll('.client_def_hd_pn_list'));
        if ($prons.length > 0) {
            searchResult.result.phsym = $prons.map(function (el) {
                var pron = '';
                var $audio = el.querySelector('.client_aud_o');
                if ($audio) {
                    pron = (($audio.getAttribute('onclick') || '').match(/https.*\.mp3/) || [''])[0];
                }
                return {
                    lang: getText(el, '.client_def_hd_pn'),
                    pron: pron,
                };
            });
            searchResult.audio = searchResult.result.phsym.reduce(function (audio, _a) {
                var lang = _a.lang, pron = _a.pron;
                if (/us|美/i.test(lang)) {
                    audio.us = pron;
                }
                else if (/uk|英/i.test(lang)) {
                    audio.uk = pron;
                }
                return audio;
            }, {});
        }
    }
    // definitions
    if (options.cdef) {
        var $container = doc.querySelector('.client_def_container');
        if ($container) {
            var $defs = Array.from($container.querySelectorAll('.client_def_bar'));
            if ($defs.length > 0) {
                searchResult.result.cdef = $defs.map(function (el) { return ({
                    pos: getText(el, '.client_def_title_bar', transform),
                    def: getText(el, '.client_def_list', transform),
                }); });
            }
        }
    }
    // tense
    if (options.tense) {
        var $infs = Array.from(doc.querySelectorAll('.client_word_change_word'));
        if ($infs.length > 0) {
            searchResult.result.infs = $infs.map(function (el) { return (el.textContent || '').trim(); });
        }
    }
    if (options.sentence > 0) {
        var $sens = doc.querySelectorAll('.client_sentence_list');
        var sentences = [];
        for (var i = 0; i < $sens.length && sentences.length < options.sentence; i++) {
            var el = $sens[i];
            var mp3 = '';
            var $audio = el.querySelector('.client_aud_o');
            if ($audio) {
                mp3 = (($audio.getAttribute('onclick') || '').match(/https.*\.mp3/) || [''])[0];
            }
            el.querySelectorAll('.client_sen_en_word').forEach(function ($word) {
                $word.outerHTML = getText($word);
            });
            el.querySelectorAll('.client_sen_cn_word').forEach(function ($word) {
                $word.outerHTML = getText($word, transform);
            });
            el.querySelectorAll('.client_sentence_search').forEach(function ($word) {
                $word.outerHTML = "<span class=\"dictBing-SentenceItem_HL\">".concat(getText($word), "</span>");
            });
            sentences.push({
                en: getInnerHTML(HOST, el, '.client_sen_en'),
                chs: getInnerHTML(HOST, el, {
                    selector: '.client_sen_cn',
                    transform: transform,
                }),
                source: getText(el, '.client_sentence_list_link'),
                mp3: mp3,
            });
        }
        searchResult.result.sentences = sentences;
    }
    if (Object.keys(searchResult.result).length > 2) {
        return searchResult;
    }
    return handleNoResult();
}
export function handleMachineResult(doc, transform) {
    var mt = getText(doc, '.client_sen_cn', transform);
    if (mt) {
        return {
            result: {
                type: 'machine',
                mt: mt,
            },
        };
    }
    return handleNoResult();
}
export function handleRelatedResult(doc, config, transform) {
    var searchResult = {
        result: {
            type: 'related',
            title: getText(doc, '.client_do_you_mean_title_bar', transform),
            defs: [],
        },
    };
    doc.querySelectorAll('.client_do_you_mean_area').forEach(function ($area) {
        var $defsList = $area.querySelectorAll('.client_do_you_mean_list');
        if ($defsList.length > 0) {
            searchResult.result.defs.push({
                title: getText($area, '.client_do_you_mean_title', transform),
                meanings: Array.from($defsList).map(function ($list) {
                    var word = getText($list, '.client_do_you_mean_list_word', transform);
                    return {
                        href: "https://cn.bing.com/dict/search?q=".concat(word),
                        word: word,
                        def: getText($list, '.client_do_you_mean_list_def', transform),
                    };
                }),
            });
        }
    });
    if (searchResult.result.defs.length > 0) {
        return searchResult;
    }
    return handleNoResult();
}
